import { url, model } from '@/configs/global'

class Membro extends model{
    constructor(data, axios) {
        super()
        for (const key in data) {
            if (Object.hasOwnProperty.call(data, key)) {
                this[key] = data[key]
            }
        }
        this.axios = axios
        this.model_type = 'membro'
        this.listaCriacao = []
        this.listaAtualizacao = [
            {label: 'level_permission', type: 'text'}
        ]
    }

    async update({getters}){
        const id_e = getters['empresa.get'].id
        return await this.axios.put(`${url}/api/${id_e}/update_user_company/${this.member_id}`, {
            level_permission: this.level_permission
        })
    }
}

export default Membro