<template>
   <div class="patrimonio-tabela">
		<TabelaGeralFiltro :aba="aba" />
		<div class="tabela">
			
			<va-data-table hoverable :items="getObjetos" :columns="columns">
				<!-- GERAL -->
				<template #cell(created_at)="{ rowIndex }">
					{{timestamp(objetos[rowIndex].created_at)}}
				</template>
				<template #cell(updated_at)="{ rowIndex }">
					{{timestamp(objetos[rowIndex].updated_at)}}
				</template>
				<template #cell(actions)="{ rowIndex }">
				<div class="actions-buttons">
					<va-button color="secondary" flat icon="edit" @click="update(objetos[rowIndex])" size="small" />
					<va-button v-if="['Ativos', 'Reports', 'Ordens de Serviço'].includes(aba.nome)" color="secondary" flat icon="image" @click="getAllPhotos(objetos[rowIndex])" size="small" />
					<va-button v-if="['Inspeções'].includes(aba.nome)" color="secondary" flat icon="visibility" @click="showInspecao(objetos[rowIndex])" size="small" />
					<va-button v-if="['Checklists', 'Planos'].includes(aba.nome)" flat color="secondary" size="small" icon="visibility" @click="showChildren(objetos[rowIndex])"></va-button>
					<va-button v-if="['Ordens de Serviço'].includes(aba.nome)" flat color="secondary" size="small" icon="settings" @click="showServicoConfig(objetos[rowIndex])"></va-button>
					<va-button v-if="['Ordens de Serviço'].includes(aba.nome)" flat color="secondary" size="small" icon="print" @click="baixarOS(objetos[rowIndex])"></va-button>
					<!--<va-button v-if="aba.tipo=='inspecoes'" @click="showInspecao(objetos[rowIndex])" size="small" >Abrir</va-button>-->
				</div>
				</template>
				<!-- ATIVOS -->
				<template v-if="aba.nome == 'Ativos'" #cell(coordinates)="{ rowIndex }">
					<va-button v-if="objetos[rowIndex].coordinates" color="secondary" flat icon="place" @click=maps(objetos[rowIndex].coordinates.latitude,objetos[rowIndex].coordinates.longitude) size="small" />
				</template>
				<template v-if="aba.nome == 'Ativos'" #cell(parent_ativo)="{ rowIndex }">
					{{objetos[rowIndex].parent_ativo ? objetos[rowIndex].parent_ativo.description : ''}}	
				</template>
				<!-- REPORTS -->
				<template v-if="aba.nome == 'Reports' || aba.nome == 'Ordens de Serviço' || aba.nome == 'Inspeções' || aba.nome == 'Chamados'" #cell(ativo)="{ rowIndex }">
					{{objetos[rowIndex].ativo ? objetos[rowIndex].ativo.description : ''}}
				</template>
				<template v-if="aba.nome == 'Reports'" #cell(criticidade)="{ rowIndex }">
					{{objetos[rowIndex].criticidade.slice(2)}}
				</template>
				<template v-if="aba.nome == 'Reports' || aba.nome == 'Chamados'" #cell(finished)="{ rowIndex }">
					<va-icon size="medium" :name="getObjetos[rowIndex].finished? 'done' : 'close'" :color="getObjetos[rowIndex].finished? 'verde' : 'laranja'"/>
				</template>
				<!-- SERVICOS -->
				<template v-if="aba.nome == 'Ordens de Serviço'" #cell(responsible)="{ rowIndex }">
					{{objetos[rowIndex].responsible ? objetos[rowIndex].responsible.name: ''}}
				</template>
				<template v-if="aba.nome == 'Ordens de Serviço'" #cell(request)="{ rowIndex }">
					{{getRequest(objetos[rowIndex].request)}}
				</template>
				<template v-if="aba.nome == 'Ordens de Serviço' || aba.nome == 'Inspeções'" #cell(status_finish)="{ rowIndex }">
					<va-icon size="medium" :name="getObjetos[rowIndex].status_finish == 'Concluídos' ? 'done' : 'close'" :color="getObjetos[rowIndex].status_finish == 'Concluídos' ? 'verde' : 'laranja'"/>
				</template>
				<!-- MANTENEDORES -->
				<template v-if="aba.nome == 'Mantenedores'" #cell(source)="{ rowIndex }">
					{{objetos[rowIndex].source == 1 ? "Externo": objetos[rowIndex].source == 0 ? "Interno" : ""}}
				</template>
				<template v-if="aba.nome == 'Mantenedores'" #cell(coust_hour)="{ rowIndex }">
					R${{objetos[rowIndex].coust_hour}}
				</template>
				<!-- MEMBROS -->
				<template v-if="aba.nome == 'Membros'" #cell(level_permission)="{ rowIndex }">
					{{objetos[rowIndex].level_permission == 0 ? "Administrador": objetos[rowIndex].level_permission == 1 ? "Programador" :  objetos[rowIndex].level_permission == 2 ?  "Membro": objetos[rowIndex].level_permission >= 100 ?  "Convidado" : ""}}
				</template>
				<!-- RELATORIOS -->
				<template v-if="aba.nome == 'Relatórios'" #cell(actions_relatorios)="{ }">
					<va-button flat color="secondary" size="small" icon="download" ></va-button>
				</template>
				<!-- INSPEÇÕES -->
				<template v-if="aba.nome == 'Inspeções'" #cell(checklist)="{ rowIndex }">
					{{objetos[rowIndex].checklist ? objetos[rowIndex].checklist.description : ''}}
				</template>
				<!-- CHAMADOS -->
				<template v-if="aba.nome == 'Chamados'" #cell(created_by)="{ rowIndex }">
					{{objetos[rowIndex].user ? objetos[rowIndex].user.name: ''}} 
				</template>
				
				<template #cell(actions_membros)="{ rowIndex }">
					<div class="actions-buttons" v-if="getPermissao == 0">
						<va-button color="secondary" flat icon="edit" @click="update(objetos[rowIndex])" size="small" />
					</div>
				</template>				
			</va-data-table>
		</div>
	  	<!-- <div v-for="objeto in objetos" :key="objeto.id">
			<div style="padding: 10px;">{{objeto}}</div>
		</div> -->
   </div>
</template>

<script>
import { url_relatorio } from '@/configs/global'
import TabelaGeralFiltro from './TabelaGeralFiltro'
export default {
    name: 'TabelaGeral',
	components: {TabelaGeralFiltro},
	props : ['aba', 'caption', 'objetos'],
	data () {
		return {
			value: true,
    	}
	},
	computed: {
		getPermissao(){
			return this.$store.getters['membro.getPermissao']
		},
		getObjetos(){
			const objs = this.objetos.map((obj)=>{
				if (this.aba.nome == 'Ativos') return {
					...obj,
					categoria: obj.category ? obj.category.description : ''
				}
				else if (this.aba.nome == 'Inspeções') return {
					...obj,
					status_finish: obj.finished_at == null ? 'Em aberto' : 'Concluídos',
				}
				else if (this.aba.nome == 'Ordens de Serviço') return {
					...obj,
					status_finish: obj.finish ? 'Concluídos' : 'Em aberto',
					inicio: `${obj.dt_start_scheduled} `.replaceAll(':00 ',''),
					fim: `${obj.dt_end_scheduled} `.replaceAll(':00 ',''),
				}
				else if (['Checklists', 'Planos'].includes(this.aba.nome)){
					return {
						...obj,
						privacidade: obj.public ? 'Público' : 'Privado',
						tipo: obj.type == 1 ? 'Manut.' : obj.type == 2 ? 'Oper.' : 'Cont.'
					}
				}
				else return obj
			})
			return objs
		},
		columns(){
			if(this.aba.columns) return this.aba.columns
			return []
		}
	},
	methods:{
		update(objeto){
			this.$store.dispatch('edit-modal.editShow', {
				objeto: objeto,
                callback: this.aba.callback
			})
		},
		async getAllPhotos(objeto){
			this.$store.dispatch('evidencias.show', objeto)
		},
		timestamp(time_str){
			var date = new Date(time_str)
			return date.toLocaleDateString('pt-BR')
		},
		maps(latitude, longitude){
			if (latitude != null && latitude != undefined && longitude != null && longitude != undefined){
				const url = `https://www.google.com/maps/place/${latitude}${longitude}/@${latitude}${longitude}`
				window.open(url, "_blank");
			}
		},
		getRequest(request){
			let slice = request.slice(0,50)
			if (slice == request) return request
			else return `${slice} ...`
		},
		async showChildren(obj){  
			this.$store.dispatch('loading.show')
			let objetos = await obj.getChildren({getters: this.$store.getters})
			this.$store.dispatch('documentos.show', {objetos: objetos, parente: obj, aba: this.aba})
			this.$store.dispatch('loading.hide')
		},
		showServicoConfig(obj){
			this.$store.dispatch('servicos.showConfig', obj)
		},
		baixarOS(obj){
			if (confirm("Deseja baixar o PDF da OS?") == true) {
				console.log(obj)
				const url_download = `${url_relatorio}/api/${obj.company_id}/ordem_servico/${obj.id}`
				window.open(url_download, "_blank");
			}
		},
		async showInspecao(objeto){
			this.$store.dispatch('loading.show');
			const secoes = await objeto.getChildren({getters: this.$store.getters})
			this.$store.dispatch('loading.hide');
			this.$store.dispatch('inspecoes.show', {objeto: objeto, secoes: secoes})
			console.log(secoes)
		}
	}
}
</script>

<style scoped>
.patrimonio-tabela {
  /*margin-top: 10px;*/
  grid-area: patrimonio-tabela;
  background-color: var(--cinza);
}
.tabela{
	color: var(--c3);
	--va-data-table-thead-border: 2px solid var(--c1);
}

.actions-buttons{
	padding-right: 15px;
}
</style>