export default {
    state: () => ({
        ABAS: {
            '/sistema/dashboard': [
                {nome: 'Dashboard de Gerenciamento', tipo: 'dashboard', description: 'Painel de gerencialmento da sua empresa'},
            ],
            '/sistema/patrimonio': [
                {nome: 'Ativos', tipo: 'ativos', model: 'ativo', callback: 'ativos.load', 
                    description: 'Cadastre aqui todos os seus ativos tangíveis e intangíveis',
                    columns: [
                    { key: 'description' , label:'DESCRIÇÃO', sortable: true },
                    { key: 'patrimony' , label: 'N° PATRIMONIO', sortable: true },
                    { key: 'parent_ativo' , label: 'Ativo Pai', sortable: true },
                    { key: 'coordinates', label: 'LOCALIZAÇÃO'},
                    { key: 'categoria', label:'CATEGORIA', sortable: true },
                    { key: 'actions', label: 'AÇÕES'}                    
                ]},
                {nome: 'Planos', tipo: 'planos', model: 'plano', callback: 'planos.load', getters: 'planos.get',
                description: 'Cadastre aqui toda a sua documentação de planos de execução de serviços',
                columns: [
					{ key: 'description' , label:'Nome', sortable: true },
					{ key: 'tipo' , label: 'Tipo', sortable: true },
					{ key: 'privacidade' , label:'Privacidade', sortable: true },
					{ key: 'created_at' , label:'Criado em:', sortable: true },
					{ key: 'updated_at' , label:'Atualizado em:', sortable: true },
					{ key: 'actions', label: 'AÇÕES'}
				]},
                {nome: 'Checklists', tipo: 'checklists', model: 'checklist', callback: 'checklists.load', getters: 'checklists.get', 
                description: 'Cadastre aqui toda a sua documentação de checklists de execução de inspeções',
                columns: [
					{ key: 'description' , label:'Nome', sortable: true },
					{ key: 'privacidade' , label:'Privacidade', sortable: true },
					{ key: 'created_at' , label:'Criado em:', sortable: true },
					{ key: 'updated_at' , label:'Atualizado em:', sortable: true },
					{ key: 'actions', label: 'AÇÕES'}
				]},
            ],
            '/sistema/empresa': [
                {nome: 'Principal', tipo: 'principal', model: null, callback: null, getters: 'empresa.get'},
                {nome: 'Setores', tipo: 'setores', model: 'setor', callback: 'setores.load', getters: 'setores.get',
                description: 'Cadastre aqui os setores da sua empresa',
                columns: [
					{ key: 'description' , label:'Nome', sortable: true },
					{ key: 'created_at' , label:'Criado em:', sortable: true },
					{ key: 'updated_at' , label:'Atualizado em:', sortable: true },
					{ key: 'actions', label: 'AÇÕES'}
				]},
                {nome: 'Projetos', tipo: 'projetos', model: 'projeto', callback: 'projetos.load', getters: 'projetos.get',
                description: 'Cadastre aqui os projetos vigentes na sua empresa',
                columns: [
					{ key: 'description' , label:'Nome', sortable: true },
					{ key: 'created_at' , label:'Criado em:', sortable: true },
					{ key: 'updated_at' , label:'Atualizado em:', sortable: true },
					{ key: 'actions', label: 'AÇÕES'}
				]},
                {nome: 'Membros', tipo: 'membros', model: null, callback: 'membros.load', getters: 'membros.get', 
                description: 'Os usuários que tem acesso a esse workspace estão listados aqui',
                columns: [
					{ key: 'name' , label:'Nome', sortable: true },
					{ key: 'email' , label:'Endereço eletrônico', sortable: true },
					{ key: 'level_permission' , label:'Nível de permissão', sortable: true },
					{ key: 'created_at' , label:'Criado em:', sortable: true },
					{ key: 'updated_at' , label:'Atualizado em:', sortable: true },
					{ key: 'actions_membros', label: 'AÇÕES'}
				]},
                {nome: 'Mantenedores', tipo: 'mantenedores', model: 'mantenedor', callback: 'mantenedores.load', getters: 'mantenedores.get', 
                description: 'Cadastre aqui a sua equipe de mantenedores',
                columns: [
					{ key: 'name' , label:'Nome', sortable: true },
					{ key: 'function' , label:'Função', sortable: true },
					{ key: 'coust_hour' , label:'Custo', sortable: true },
					{ key: 'source', label: 'ORIGEM', sortable: true},
					{ key: 'actions', label: 'AÇÕES'}	
				]},
                {nome: 'Relatórios', tipo: 'relatorios', model: null, callback: 'relatorios.load', getters: 'relatorios.get',
                description: 'Faça o download dos seus relatórios aqui ',    
                },
            ],
            '/sistema/servicos': [
                {nome: 'Reports', tipo: 'reports', model: 'report', callback: 'reports.load',
                description: 'Cadastre aqui anomalias e oportunidades',
                columns: [
                    { key: 'anomaly' , label:'ANOMALIA', sortable: true },
                    { key: 'ativo' , label: 'ATIVO', sortable: true},
                    { key: 'criticidade', label:'CRITICIDADE',  sortable: true },
                    { key: 'created_at', label:'CRIADO EM', sortable: true},
                    { key: 'finished', label:'STATUS', sortable: true},
                    { key: 'actions', label: 'AÇÕES'}
                ]},
                {nome: 'Ordens de Serviço', tipo: 'servicos', model: 'servico', callback: 'servicos.load', 
                description: 'Cadastre aqui toda a sua lista de ordens de serviços ',
                columns: [
                    { key: 'request' , label:'REQUISIÇÃO', sortable: true},
                    { key: 'ativo' , label: 'ATIVO', sortable: true},
                    { key: 'responsible', label:'RESPOSÁVEL', sortable: true},
                    { key: 'inicio', label:'Início', sortable: true},
                    { key: 'fim', label:'Fim', sortable: true},
                    { key: 'status_finish' , label:'STATUS',sortable: true},
                    { key: 'actions', label: 'AÇÕES'}
                ]},
                {nome: 'Inspeções', tipo: 'inspecoes', model: 'inspecao', callback: 'inspecoes.load', getters: 'inspecoes.get', 
                description: 'Cadastre aqui toda a sua lista de inspeções ',
                columns: [
					{ key: 'description' , label:'Descrição', sortable: true },
                    { key: 'ativo' , label:'Ativo', sortable: true },
                    { key: 'checklist' , label:'Checklist', sortable: true },
                    { key: 'status_finish' , label:'STATUS',sortable: true},
					{ key: 'actions', label: 'AÇÕES'}
				]},
                {nome: 'Chamados', tipo: 'chamados', model: 'chamado', callback: 'chamados.load', getters: 'chamados.get',
                description: 'Abra um chamado aqui',
                columns: [
					{ key: 'description' , label:'Descrição', sortable: true },
                    { key: 'ativo' , label:'Ativo', sortable: true },
                    { key: 'created_by' , label:'Aberto por', sortable: true },
                    { key: 'finished', label:'STATUS', sortable: true},
					{ key: 'actions', label: 'AÇÕES'}
				]}
            ]
        },
        INDICES: {},
        FILTRO: {}
    }),
    mutations: {
        'abas.set': function(state, arg){
            const key = arg[0]
            const val = arg[1]
            state.INDICES[key] = val
        },
        'abas.setFiltro': function(state, arg){
            const key = arg[0]
            const val = arg[1]
            let ant = state.FILTRO[key]
            if (ant == undefined) ant = {}
            state.FILTRO[key] = {
                ...ant,
                ...val
            }
        }
    },
    getters: {
        'abas.getIndices': function(state){
            return state.INDICES
        },
        'abas.getAbas': function(state){
            return state.ABAS
        },
        'abas.getFiltro': function(state){
            return state.FILTRO
        }
    },
    actions: {
        'abas.set': function({commit}, arg){
            commit('abas.set', arg)
        },
        'abas.setFiltro': function({commit}, arg){
            commit('abas.setFiltro', arg)
        }
    }
}