<template>
	<div class="dashboard-card" >
		<div>
		<div class="card-title">
			<h1> {{card.title}} </h1>
			<p v-if="card.title == 'Chamados'"> Total de  {{resume.total_items}} chamados</p>
			<p v-if="card.title == 'Serviços'"> Total de  {{resume.total_services}} serviços</p>
			<p v-if="card.title == 'Reports'"> Total de  {{resume.total_reports}} reports</p>
			<p v-if="card.title == 'Inspeções'"> Total de  {{resume.total_inspections}} inspeções</p>
		</div>
		<div class="card-items">
			<span v-for="dado in getDados()" :key="dado.subtitle" >
				<DashboardCardItems  :icon="dado.icon" :subtitle="dado.subtitle" :number="dado.number" :color="dado.color"/>
			</span>
		</div>
		</div>
		<div class="chart">
			<DashboardChartPie :resume="resume" :dados="getDados()" :color="getColor()"/>
		</div>
	</div>
</template>

<script>
import DashboardCardItems from './DashboardCardItems.vue'
import DashboardChartPie from './DashboardChartPie.vue'
export default {
	name: 'DashboardCard',
	props : ['card','resume'],
	components: {
		DashboardCardItems,
		DashboardChartPie
  	},
    computed: {
        getEmpresa(){
			return this.$store.getters['empresa.get']
		}, 
    }, 
	watch: {
		getEmpresa(value){
			this.empresa = value
		}
	},
	methods:{
		load(){
			this.empresa = this.getEmpresa
		},
		getDados(){
			let DADOS = []
			if(this.card.title == 'Chamados'){
				DADOS = DADOS.concat({subtitle: 'concluídos', color: 'var(--verde)', number: this.resume.total_concluido, icon: 'call' })
				DADOS = DADOS.concat({subtitle: 'em aberto', color: 'var(--amarelo)', number: this.resume.total_em_aberto, icon: 'call'})
				DADOS = DADOS.concat({subtitle: 'em andamento', color: 'var(--azul)', number: this.resume.total_em_andamento, icon: 'call'})
				DADOS = DADOS.concat({subtitle: 'rejeitados', color: 'var(--laranja)', number: this.resume.total_rejeitado , icon: 'call'})
				
			}
			else if(this.card.title == 'Serviços'){
				DADOS = DADOS.concat({subtitle: 'concluídos', color: 'var(--verde)', number: this.resume.total_concluido, icon:'construction'})
				DADOS = DADOS.concat({subtitle: 'em aberto', color: 'var(--amarelo)', number: this.resume.total_em_aberto, icon:'construction'})
				DADOS = DADOS.concat({subtitle: 'não programados', color: 'var(--azul)', number: this.resume.total_nao_programado, icon:'construction'})
				DADOS = DADOS.concat({subtitle: 'em atraso', color: 'var(--laranja)', number: this.resume.total_atrasado, icon:'construction'})
				DADOS = DADOS.concat({subtitle: 'cancelados', color: '#181a21', number: this.resume.total_cancelado, icon:'construction'})
			}
			else if(this.card.title == 'Reports'){
				DADOS = DADOS.concat({subtitle: 'finalizados', color: 'var(--verde)', number: this.resume.total_finalizado, icon: 'report' })
				DADOS = DADOS.concat({subtitle: 'em aberto', color: 'var(--amarelo)', number: this.resume.total_em_aberto, icon: 'report'})
				DADOS = DADOS.concat({subtitle: 'cancelados', color: '#181a21', number: this.resume.total_cancelado, icon: 'report'})
			}
			else if(this.card.title == 'Inspeções'){
				DADOS = DADOS.concat({subtitle: 'finalizados', color: 'var(--verde)', number: this.resume.total_finalizado, icon:'travel_explore'})
				DADOS = DADOS.concat({subtitle: 'em aberto', color: 'var(--amarelo)', number: this.resume.total_em_aberto, icon:'travel_explore'})
				DADOS = DADOS.concat({subtitle: 'cancelados', color: '#181a21', number: this.resume.total_cancelado, icon:'travel_explore'})
			}
			return DADOS
		},
		getColor(){
			let color = []
			if(this.card.title == 'Chamados') color = ["#3CDE93","#FFDDAA","#3CB3DE", "#F85B40"]
			else if(this.card.title == 'Serviços') color = ["#3CDE93","#FFDDAA","#3CB3DE", "#F85B40", "#181a21"]
			else if(this.card.title == 'Reports') color = ["#3CDE93","#FFDDAA", "#181a21"]
			else if(this.card.title == 'Inspeções') color = ["#3CDE93","#FFDDAA", "#181a21"]
			return color
		}
	},
	mounted() {
		this.load()
	},
}
</script>

<style scoped>

.dashboard-card {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.card-title {
	color: #CCCCCC90;
	text-align: center;
	padding: 5px;
}

.card-title > h1 {
	font-size: 1.1rem;
	color: var(--c3);
}

.card-items {
	width: 100%;
}

.chart{
	width: 100%;
	height: 220px;
}
</style>