import Membro from '@/models/membro'
import { url } from '@/configs/global'

export default {
    state: () => ({
        membros: [],
        permissao: 999
    }),
    mutations: {
        'membros.set': function(state, {membros, permissao}){
            if(membros != undefined) state.membros = membros
            if(permissao != undefined) state.permissao = permissao
        },
    },
    getters: {
        'membros.get': function(state){
            return state.membros
        },
        'membro.getPermissao': function(state){
            return state.permissao
        }
    },
    actions: {
        'membros.load': function( { dispatch, getters, commit}){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
			this.axios.get(`${url}/api/${id_e}/getall_members_company`)
			.then((response) => {
                const membros = response.data.data.map(obj => new Membro({...obj.user, member_id: obj.id, level_permission: obj.level_permission}, this.axios)).sort((a, b) => a.name.localeCompare(b.name))
                const membro = membros.filter(e=>e.id==getters['usuario.get'].id)
                commit('membros.set', {
                    membros: membros,
                    permissao: membro.length > 0 ? membro[0].level_permission : 999
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        }
    }
}