import Chamado from '@/models/chamado'
import { url, get_queries } from '@/configs/global'

export default {
    state: () => ({
        chamados: [],
        last_page: 1
    }),
    mutations: {
        'chamados.set': function(state, {chamados, last_page}){
            if(chamados != undefined) state.chamados = chamados
            if(last_page != undefined) state.last_page = last_page
        },
    },
    getters: {
        'chamados.get': function(state){
            return state.chamados
        },
        'chamados.getLastPage': function(state){
            return state.last_page
        }
    },
    actions: {
        'chamados.load': function( { dispatch, getters, commit}, args){
            dispatch('loading.show');
			const id_e = getters['empresa.get'].id
			this.axios.get(`${url}/api/${id_e}/get_calleds?${get_queries(args)}`)
			.then((response) => {
                commit('chamados.set', {
                    last_page: response.data.data.last_page,
                    chamados: response.data.data.data.map(obj => new Chamado(obj, this.axios)).sort((a, b) => a.description.localeCompare(b.description))
                })
			}).catch((err)=>{
				dispatch('toast.error', err);
			}).finally(()=>{
				setTimeout(() => {
					dispatch('loading.hide');
				}, 300);
			})
        }
    }
}